body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eee;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, h1, h2, h3, h4, h5, h6 {
  font-weight: 300;
  line-height: 1.5em;
  color: #3C4858;
}

h2 {
  font-size: 2.25rem;
}

h3 {
  font-size: 1.5625rem;
}

h4 {
  font-size: 1.125rem;
}

p {
  color: #999;
  font-size: 1rem;
}
.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    flex-direction: column;
}

.main-content {
    flex:  1 0 auto;
}
nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: rgb(255, 255, 255);
    background-color: white;
    position: fixed;
    top: 0;
    width: 100%;
    height: 8%;
    transition: 0.5s;
    background: rgba(0, 0, 0, 0);
    z-index: 3;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
}

nav > h1 {
    color: #fff;
}

nav.full-opacity {
    background: #02537e;
}

#logo {
    text-align: center;
    display: block;
    font-size: 1.3em;
    margin-left: 1rem;
    margin-right: 3rem;
}

#logo>a {
    text-decoration: none;
    color: inherit;
}

.backdrop-fade-in {
    opacity: 1;    
    transition: opacity 0.3s linear;
    -webkit-transition: opacity 0.3s linear;
}

.backdrop-fade-out {
    opacity: 0;
    transition: opacity 0.3s linear;
    -webkit-transition: opacity 0.3s linear;
    pointer-events: none
}
/* Shadow 0dp */
.elevation-0 {
    box-shadow: none;
}

/* Shadow 1dp */
.elevation-1 {
    box-shadow: 0 1px 1px 0 rgba(0,0,0,0.14), 0 2px 1px -1px rgba(0,0,0,0.12), 0 1px 3px 0 rgba(0,0,0,0.20);
}

/* Shadow 2dp */
.elevation-2 {
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.20);
}

/* Shadow 3dp */
.elevation-3 {
    box-shadow: 0 3px 4px 0 rgba(0,0,0,0.14), 0 3px 3px -2px rgba(0,0,0,0.12), 0 1px 8px 0 rgba(0,0,0,0.20);
}

/* Shadow 4dp */
.elevation-4 {
    box-shadow: 0 4px 5px 0 rgba(0,0,0,0.14), 0 1px 10px 0 rgba(0,0,0,0.12), 0 2px 4px -1px rgba(0,0,0,0.20);
}

/* Shadow 6dp */
.elevation-6 {
    box-shadow: 0 6px 10px 0 rgba(0,0,0,0.14), 0 1px 18px 0 rgba(0,0,0,0.12), 0 3px 5px -1px rgba(0,0,0,0.20);
}

/* Shadow 8dp */
.elevation-8 {
    box-shadow: 0 8px 10px 1px rgba(0,0,0,0.14), 0 3px 14px 2px rgba(0,0,0,0.12), 0 5px 5px -3px rgba(0,0,0,0.20) !important;
}

/* Shadow 9dp */
.elevation-9 {
    box-shadow: 0 9px 12px 1px rgba(0,0,0,0.14), 0 3px 16px 2px rgba(0,0,0,0.12), 0 5px 6px -3px rgba(0,0,0,0.20);
}

/* Shadow 12dp */
.elevation-9 {
    box-shadow: 0 12px 17px 2px rgba(0,0,0,0.14), 0 5px 22px 4px rgba(0,0,0,0.12), 0 7px 8px -4px rgba(0,0,0,0.20);
}

/* Shadow 16dp */
.elevation-16 {
    box-shadow: 0 16px 24px 2px rgba(0,0,0,0.14), 0 6px 30px 5px rgba(0,0,0,0.12), 0 8px 10px -5px rgba(0,0,0,0.20);
}

/* Shadow 24dp */
.elevation-24 {
    box-shadow: 0 24px 38px 3px rgba(0,0,0,0.14), 0 9px 46px 8px rgba(0,0,0,0.12), 0 11px 15px -7px rgba(0,0,0,0.20);
}

.backdrop {
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
    cursor: pointer;
}

#nav-menu {      
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
}

#nav-menu > li {    
    padding: 0.5rem 1rem;
}

#nav-menu > li > a {
	text-decoration: none;
	text-transform: uppercase;
	text-align: center;
    display: block;
	font-size: .99em;
	text-align: center;
	text-transform: uppercase;
    color: #FFFFFF;
}

#nav-menu > li:active {
    background-color: #00000024;
}

#nav-menu.nav-menu-horizontal {     
    display: none;
}

#nav-menu.nav-menu-vertical {     
    padding-top: 1rem;   
    right: 0;
    top: 0;
    flex-direction: column;
    width: 260px;
    background-color: initial;
    position: fixed;
    height: 100vh;
    z-index: 3;
    background-color: #fff;
    transition: -webkit-transform .3s ease;
    transition: transform .3s ease;
    transition: transform .3s ease, -webkit-transform .3s ease;
    transform: translateX(100%);
    -webkit-transform: translateX(100%);
}

#nav-menu.nav-menu-vertical > li > a {
    color: #212121;
}

.social-icon {
    width: 30px;
}

#nav-menu.nav-menu-vertical.vertical-show {
    transform: translateX(0%);
    -webkit-transform: translateX(0%);
}

#nav-menu.nav-menu-horizontal.horizontal-show {
    display: flex;
}
#burger {
    position: fixed;
    right: 1rem;
    display: none;
    flex-direction: column;
    justify-content: space-around;
    width: 2rem;
    height: 2rem;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    z-index: 5;
}

#burger > div {
    width: 2rem;
    height: 0.25rem;
    border-radius: 10px;
    position: relative;    
    transition: all 0.3s linear;
    -webkit-transform-origin: 1px;
            transform-origin: 1px;
    background-color: #FFF;
}

#burger.burger-is-clicked > div {
    background-color: #212121;
}

.burger-is-clicked > div:first-child {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.burger-is-clicked > div:nth-child(2) {
    opacity: 0;
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
}

.burger-is-clicked > div:nth-child(3) {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

@media (max-width: 769px) {
    #burger {
        display: flex;
    }
}
header {
    background-color: #02537e;
    min-height: 50vh;
    display: flex;
    flex-direction: column;  
    justify-content: center;
    align-content: flex-start;
    align-items: flex-start;
    padding-left: 5rem;
    padding-right: 5rem;
    background-image: url(/hideawaymarinapanamacity-react/static/media/header.7040c22e.jpg);
    background-repeat: no-repeat; 
    background-position: right;
}

.header-title {
    width: 40%;
}

header h2 {
    margin-top: 3.5rem;
    margin-bottom: 0;
}

header h2, header p {
    color: #fff;
}

@media only screen and (max-width: 769px){
    header {
        padding: 1rem;
        padding-top: 0;
    }
}

@media only screen and (max-width: 1200px){
    .header-title {
        width: 100%
    }
}

#google-map-container iframe {
    width: 50vh;
    height: 50vh;
}
footer {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 1rem;
    justify-content: center;
    align-items: center;
    color: #757575;
}

.section {
    padding: 2rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.section > h2 {
    font-size: 2.25rem;
    line-height: 1.5em;
    text-align: center;
}

.section + * {
    border-top: 0.2rem solid #BDBDBD;
}

@media only screen and (max-width: 769px){
    .section {
        padding: 0.5rem;
    }
}

.card {
    display: flex;
    flex-direction: row;
    margin: 1rem;
    border-radius: 0.5rem;
    padding: 1rem 2rem 2rem 1rem;
    background-color: #fff;
    text-align: center;
    width: 100%;
    box-sizing: border-box;
}

.card-content {
    display: flex;
    flex-direction: column;   
    align-items: center;
    justify-content: center;
}

.card-carousel {
    margin: 1rem;
    width: 60%;
}

.card-content > h3, h4 {
    margin: 1rem;
}

.card-content > p {
    margin: 1rem;
    text-align: center;
    white-space: pre-wrap;
}

@media only screen and (max-width: 1078px){
    .card {
        flex-direction: column;
        align-items: center;
        padding: 1rem;
    }

    .card-carousel {
        width: 100%;
    }

    .card-content > h3, h4 {
        margin: 0.5rem;
    }
}

@media only screen and (max-width: 769px){

    .card-carousel {
        margin: 0.5rem;
    }
}
.legend {
    bottom: 0.5rem !important;
    border-radius: 0 !important;
}
.book-button-group {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.vrbo  {
    background-color: rgb(2, 83, 126);
}

.book-button-group .button {
    margin-right: 1rem;
}

.book-button-group .button:last-child {
    margin-right: 0;
}

@media only screen and (max-width: 769px){
    .book-button-group {
        flex-direction: column;
    }

    .book-button-group .button {
        margin: 0 0 1rem 0;
    }
}

#about-me .card {
    flex-direction: column;
    align-items: center;
}

#about-me .card img {
    border-radius: 50%;
    width: 20%;
    margin: 1rem;
}

#about-me .card-content {
    max-width: 50%;
}

#about-me .card-content > p {
    white-space: normal;
}

@media only screen and (max-width: 1078px){
    #about-me .card-content {
        max-width: 100%;
    }
}
